import React, { useRef } from "react";
import "animate.css";
import "../assets/styles/Sponsors.css";
import "../assets/styles/AboutUs.css";
import AboutUsSliding from "./AboutUsSliding";
import SecHeader from "../components/UI/SecHeader";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import downarrow from "../assets/images/downarrow.png";
import Anim from "../anim.json";
import Lottie from "lottie-react";

const AboutUs = () => {
  const AboutSection = useRef(null);
  const gotoAboutSection = () =>
    window.scrollTo({
      top: AboutSection.current.offsetTop,
      behavior: "smooth",
  });
  return (
    <>
      {/* <SecHeader
        title="ABOUT US"
        imageurl={
          "https://i.pinimg.com/originals/ad/a9/bf/ada9bfbe35626308297a070e538dc4e9.jpg"
        }
      /> */}
      <section className="sponsors-hero-container">
        <img
          className="sponsors-header-img"
          src="https://i.pinimg.com/originals/ad/a9/bf/ada9bfbe35626308297a070e538dc4e9.jpg"
        />
        <div className="sponsors-hero-info px-20 py-40">
          <h1 className="sponsors-hero-title md:text-8xl sm:text-6xl xsm:text-5xl text-bolder font-oswald mx-auto flex justify-start items-center">
            ABOUT US
          </h1>
          <div
            className="pb-2 mx-auto bg-tzpurple rounded-tr-lg flex justify-start items-center h-12 sm:w-60  hover:opacity-50 cursor-pointer mt-48 xsm:w-full flex justify-center items-center scroll-to-main"
            onClick={gotoAboutSection}
          >
            <Lottie animationData={Anim} />
          </div>
        </div>
      </section>
      <div className="bg-bgBlue text-white flex flex-col items-center justify-between space-y-12 md:flex-col md:space-y-0" ref={AboutSection}>
        <div className="lines">
          <div className="diamond"></div>
        </div>
        <div className="mx-20 text-center sm:mx-10 xsm:mx-5 md:-mt-20">
          <p className="text-grayishWhite text-lg mt-20 mx-auto text-center font-mont sm:mx-24 xsm:mx-20 xl:px-40">
            Lorem Ipsum is simply dummy text of the printing and typesetting
            industry. Lorem Ipsum has been the industry's standard dummy text
            ever since the 1500s
          </p>
        </div>
        <div className="sliding-col container flex flex-col align-center justify-between py-8 mx-auto space-y-4 xl:px-40">
          <AboutUsSliding flow1="Right" flow2="Left" disp="text" />
          <AboutUsSliding flow1="Left" flow2="Right" disp="image" />
        </div>
      </div>
    </>
  );
};

export default AboutUs;
