import React from "react";
import { AnimationOnScroll } from "react-animation-on-scroll";
import insert from "../assets/images/tech-1.jpg";

function Sliding({ flow1, flow2, disp }) {
  if (disp === "text") {
    return (
      <div className="flex flex-col items-center justify-between md:flex-row md:items-center md:justify-between md:space-y-5">
        <AnimationOnScroll
          animateIn={`animate__fadeIn${flow1}`}
          className="flex-none ..."
        >
          <div className="mx-7 sm:space-y-5">
            <img src={insert} alt="" width="400px" height="auto" />
          </div>
        </AnimationOnScroll>
        <AnimationOnScroll animateIn={`animate__fadeIn${flow2}`}>
          <div className="flex flex-col items-center justify-center mx-7 text-white md:items-start sm:space-y-5">
            <h1 className="text-2xl font-oswald">Heading</h1>
            <p className="text-lg text-grayishWhite font-mont">
              Lorem Ipsum is simply dummy text of the printing and typesetting
              industry. Lorem Ipsum has been the industry's standard dummy text
              ever since the 1500sLorem Ipsum is simply dummy text of the
              printing and typesetting industry. Lorem Ipsum has been the
              industry's standard dummy text ever since the 1500sLorem Ipsum is
              simply dummy text of the printing and typesetting industry. Lorem
              Ipsum has been the industry's standard dummy text ever since the
              1500s Lorem Ipsum is simply dummy text of the printing and
              typesetting industry. Lorem Ipsum has been the industry's standard
              dummy text ever since the 1500s
            </p>
          </div>
        </AnimationOnScroll>
      </div>
    );
  } else {
    return (
      <div className="flex flex-col-reverse items-center justify-between md:flex-row md:items-center md:justify-between md:space-y-5">
        <AnimationOnScroll animateIn={`animate__fadeIn${flow2}`}>
          <div className="flex flex-col items-center justify-center mx-7 text-white md:items-start sm:space-y-5">
            <h1 className="text-2xl sm:text-center font-oswald">Heading</h1>
            <p className="text-lg text-grayishWhite font-mont">
              Lorem Ipsum is simply dummy text of the printing and typesetting
              industry. Lorem Ipsum has been the industry's standard dummy text
              ever since the 1500sLorem Ipsum is simply dummy text of the
              printing and typesetting industry. Lorem Ipsum has been the
              industry's standard dummy text ever since the 1500sLorem Ipsum is
              simply dummy text of the printing and typesetting industry. Lorem
              Ipsum has been the industry's standard dummy text ever since the
              1500s Lorem Ipsum is simply dummy text of the printing and
              typesetting industry. Lorem Ipsum has been the industry's standard
              dummy text ever since the 1500s
            </p>
          </div>
        </AnimationOnScroll>
        <AnimationOnScroll
          animateIn={`animate__fadeIn${flow1}`}
          className="flex-none ..."
        >
          <div className="mx-7 sm:space-y-5">
            <img src={insert} alt="" width="400px" height="auto" />
          </div>
        </AnimationOnScroll>
      </div>
    );
  }
}

export default Sliding;
