import React, { Fragment } from "react";
import { blogArray } from "../constants/blogArray";
import { useParams } from 'react-router-dom';
import BlogStandAlone from "../components/BlogStandAlone";
import SecHeader from "../components/UI/SecHeader";
const BlogDetail = () => {

    const params = useParams();
    const blogId = params.blogId;

    for (var i = 0; i < blogArray.length; ++i) {
        if (blogArray[i].id == blogId) {
            return (
                <Fragment>
                    <SecHeader title={"Blogs"} imageurl={"https://www.state.gov/wp-content/uploads/2019/04/shutterstock_563430904-2560x852.jpg"} />
                    <BlogStandAlone blog={blogArray[i]} />
                </Fragment>
            )
        }
    }

    return (
        <Fragment>
            <SecHeader title={"Blogs"} imageurl={"https://www.state.gov/wp-content/uploads/2019/04/shutterstock_563430904-2560x852.jpg"} />
            <h1>404 No Such Blog</h1>
        </Fragment>
    )
}

export default BlogDetail;