import React from "react";
import {useState} from 'react';
import '../assets/styles/TeamCard.css';

const TeamsCard=({teamName,position})=>{
  return(
    <div className="p-4 sm:w-1/2 lg:w-1/3 xsm:w-full xsm:px-0 sm:p-4 xsm:py-4 h-106 text-black font-oswald">
                  <div className="item h-full border-2 border-gray-200 border-opacity-60 rounded-lg overflow-hidden bg-white  border-none hover:border-stone-50 hover:shadow-[0px_12px_24px_4px_rgba(255,255,255,0.20)]">
                  <div className="block">
                  <div className="banner-card relative">
                    <img src="https://picsum.photos/id/188/720/400" alt="image" className="lg:h-72 md:h-48 w-full   object-cover object-center max-w-full grayscale hover:grayscale-0"></img> 
                  </div>
                <div className="p-6 hover:bg-indigo-700 hover transition duration-300 ease-in miniitem">
                  <h1 className="text-2xl font-semibold mb-3 text-center">{teamName}</h1>
                  <h2 className="text-base font-medium text-indigo-300 mb-1 text-center">{position}</h2>
                </div>
                </div>
                </div>
    </div>
  );
}

export default TeamsCard;
