import classes from './buttonStyle.module.css'
const Button = (props) => {
    return (
        <div className={classes.buttons}>
            <button className={classes.blob_btn}>
                {props.text}
                <span className={classes.blob_btn__inner}>
                    <span className={classes.blob_btn__blob} />
                    <span className={classes.blob_btn__blob} />
                    <span className={classes.blob_btn__blob} />
                    <span className={classes.blob_btn__blob} />
                    <span className={classes.blob_btn__blob} />
                </span>
            </button>
        </div>
    )
};

export default Button;