import "../../assets/styles/Sponsors.css";
const SecHeader = (props) => {
  return (
    <div
      style={{
        marginTop: "-5rem",
        backgroundColor: "#131A43",
      }}
    >
      <section className="sponsors-hero-container ">
        <img className="sponsors-header-img" src={props.imageurl} alt="" />
        <div className="sponsors-hero-info px-20 py-40">
          <div className="sponsors-hero-title-rect w-12 h-1.5 bg-black mb-2"></div>
          <h1 className="sponsors-hero-title text-6xl md:text-12xl text-bolder font-oswald">
            {props.title}
          </h1>
        </div>
      </section>
    </div>
  );
};

export default SecHeader;
