import React, { useEffect, useRef } from "react";
import "../assets/styles/Teams.css";
import TeamsCard from "./TeamsCard";
import downarrow from "../assets/images/downarrow.png";
import Anim from "../anim.json";
import Lottie from "lottie-react";

const teamInfo = [
  {
    teamName: "Team 1",
    position: "position 1",
  },
  {
    teamName: "Team 2",
    position: "position 2",
  },
  {
    teamName: "Team 3",
    position: "position 3",
  },
  {
    teamName: "Team 4",
    position: "positon 4",
  },
  {
    teamName: "Team 5",
    position: "position 5",
  },
  {
    teamName: "Team 6",
    position: "position 6",
  },
];

const Teams = () => {
  const EventSection = useRef(null);
  const gotoEventSection = () =>
    window.scrollTo({
      top: EventSection.current.offsetTop,
      behavior: "smooth",
    });

  return (
    <div className="event-container bg-bgBlue text-white">
      <section className="sponsors-hero-container ">
        <img
          className="event-header-img"
          src="https://w0.peakpx.com/wallpaper/35/885/HD-wallpaper-dark-purple-blue-shades-dark-purple.jpg"
        />
        <div className="sponsors-hero-info px-20 py-40">
          <div className="sponsors-hero-title-rect w-12 h-1.5 bg-black mb-2 mx-auto"></div>
          <h1 className="sponsors-hero-title md:text-8xl sm:text-6xl xsm:text-6xl text-bolder font-oswald mx-auto flex justify-start items-center">
            Technozion Team
          </h1>
          <div
            className="pb-2 mx-auto bg-tzpurple rounded-tr-lg flex justify-start items-center h-12 sm:w-60  hover:opacity-50 cursor-pointer mt-48 xsm:w-full flex justify-center items-center scroll-to-main"
            onClick={gotoEventSection}
          >
            <Lottie animationData={Anim} />
          </div>
        </div>
      </section>
      <section
        className="md:h-full flex items-center text-gray-60"
        ref={EventSection}
      >
        <div className="container px-5 py-24 mx-auto ">
          <div className="text-center mb-12">
            <h1 className="sponsors-hero-title text-3xl md:text-3xl text-bolder mb-1 font-oswald">
              Technozion Teams
            </h1>
          </div>
          <div className="flex flex-wrap -m-4">
            {teamInfo.map((team) => (
              <TeamsCard
                teamName={team.teamName}
                position={team.position}
                teamDesc={team.teamDesc}
              />
            ))}
          </div>
        </div>
      </section>
    </div>
  );
};

export default Teams;
