import React from "react";
import '../assets/styles/BlogAlone.css';


const BlogStandAlone = props => {
    let blog = props.blog;

    const style = {
        margin: "auto",
        width: "60%",
        padding: "10px",
    }

    return (
        <div style={style}>
            <x-sign>
                {blog.title}
            </x-sign>
            <img className="for_image" src={blog.imageurl} width={"700px"}/>
            <p className="for_content font-mont">
                {blog.content}
            </p>
        </div>
    )

}


export default BlogStandAlone;