import React from "react";
import "../assets/styles/Footer.css";
import "./FooterButtons";
import facebook from "../assets/images/facebook.png";
import instagram from "../assets/images/instagram.png";
import discord from "../assets/images/discord.png";
import tzlogo from "../assets/images/tzlogo.png";
import "animate.css";
import FooterButtons from "./FooterButtons";
import Iframe from "react-iframe";

const data1 = ["About Us", "Blogs", "Contact"];
const data2 = ["About Us", "Blogs", "Contact"];

function Footer() {
  return (
    <footer style={{ backgroundColor: "#131A43" }} className=" text-white">
      <div className="lines">
        <div className="diamond"></div>
      </div>
      <div>
        <div className="container flex flex-col items-center justify-center py-8 mx-auto space-y-6 md:flex md:flex-row md:space-y-0 md:justify-center uppercase md:items-start px-6">
          <div className="flex flex-col space-y-8 md:flex-row md:space-x-8 md:space-y-0 sm:w-full sm:justify-center xsm:w-full">
            <div className="flex flex-col space-y-3 text-white">
              <div className="text-xl mb-4 text-left font-bold">Important</div>
              {data1.map((user) => (
                <FooterButtons data={user} />
              ))}
            </div>
            <div className="flex flex-col space-y-3 text-white">
              <div className="text-xl mb-4 text-left font-bold">Important</div>
              {data2.map((user) => (
                <FooterButtons data={user} />
              ))}
            </div>
          </div>
          <div className="container flex flex-col items-center justify-center space-y-3 md:space-y-0 uppercase md:items-start lg:px-15 md:px-12 xsm:w-full sm:pt-10 md:pt-0 xsm:pt-10">
            <div className="text-xl mb-4 text-left font-bold xsm:text-center ">
              Locate Us
            </div>
            <Iframe
              src="https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d15179.588466037536!2d79.5308386!3d17.983523!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0xf1b2c37fcb8fefce!2sNational%20Institute%20of%20Technology%2C%20Warangal%2C%20Telangana!5e0!3m2!1sen!2sin!4v1632005137818!5m2!1sen!2sin"
              style="border:0;"
              className="mt-4 rounded-tr-lg lg:h-56 lg:w-56 md:h-48 md:w-48 sm:h-56 sm:w-full xsm:h-56"
              loading="lazy"
              referrerpolicy="no-referrer-when-downgrade"
            ></Iframe>
          </div>
          <div className="flex flex-col space-y-10 text-white xsm:items-center">
            <div className="flex flex-col space-y-3 text-white xsm:items-center mt-0">
              <div className="text-xl text-left font-bold mb-2">Follow Us</div>
              <div className="sm:flex sm:flex-row sm:justify-center sm:space-y-0 sm:space-x-2 xsm:flex xsm:flex-col xsm:space-y-3">
                <div className="bg-lightGreen rounded-tr-lg flex justify-center items-center h-9 lg:w-9 md:w-9 xsm:h-9 hover:opacity-70 sm:w-28  xsm:w-48">
                  <a
                    href="#"
                    className="hover:text-lightGreen hover:font-semibold"
                  >
                    <img
                      src={facebook}
                      alt=""
                      className=" bg-lightGreen pl-1 h-7 w-8"
                    />
                  </a>
                </div>
                <div className="bg-lightGreen rounded-tr-lg flex justify-start items-center xsm:h-9 h-9 lg:w-9 md:w-9 hover:opacity-70  sm:w-28 xsm:w-48">
                  <a
                    href="#"
                    className="hover:text-lightGreen hover:font-semibold"
                  >
                    <img
                      src={instagram}
                      alt=""
                      className=" bg-lightGreen pl-1 h-7 w-8 sm:ml-10 md:ml-0 xsm:ml-20"
                    />
                  </a>
                </div>
                <div className="bg-lightGreen rounded-tr-lg flex justify-start items-center h-9 lg:w-9 md:w-9 xsm:h-9 hover:opacity-70  sm:w-28  xsm:w-48">
                  <a
                    href="#"
                    className="hover:text-lightGreen hover:font-semibold"
                  >
                    <img
                      src={discord}
                      alt=""
                      className=" bg-lightGreen pl-1 h-7 w-8 sm:ml-10 md:ml-0 xsm:ml-20"
                    />
                  </a>
                </div>
              </div>
            </div>
          </div>
          {/* <div className="flex flex-col space-y-0 text-white xsm:items-center">
            <div className="text-xl mb-4 text-left font-bold">Locate Us</div>
            {/* <div className="bg-lightGreen rounded-tr-lg flex justify-start items-center xsm:w-40 xsm:h-10 sm:w-40 h-10 lg:w-40 md:w-40 hover:opacity-70 text-black font-bold">
              <a
                href="https://goo.gl/maps/BwyfwYyoicZFoChFA"
                className="hover:font-semibold pl-10 hover:text-black hover:font-bold"
              >
                Site Map
              </a>
            </div>
          </div> */}
        </div>
      </div>
    </footer>
  );
}

export default Footer;
