import React from "react";

function FooterButtons({ data }) {
  return (
    <div className="bg-gray rounded-tr-lg flex justify-start items-center h-12 lg:w-60 md:w-32 sm:w-full xsm:w-full hover:bg-lightGreen hover:text-black hover:font-bold hover:cursor-pointer">
      <a
        href="#"
        className=" hover:font-bold hover:text-black pl-3 hover:text-lightGreen"
      >
        {data}
      </a>
    </div>
  );
}

export default FooterButtons;
