import React from "react";
import { useState } from "react";
import "../assets/styles/TeamCard.css";

const EventCard = ({
  eventName,
  eventDate,
  eventDesc,
  liveDisp,
  eventImage,
}) => {
  const [isShown, setIsShown] = useState(false);

  const handleClick = (event) => {
    setIsShown((current) => !current);
  };
  return (
    <div className="p-4 sm:w-1/2 lg:w-1/3 xsm:px-0 sm:p-4 xsm:py-4 h-106 text-black font-oswald">
      {!isShown && (
        <div className="item h-full border-2 border-gray-200 border-opacity-60 rounded-lg overflow-hidden bg-white  border-none hover:border-stone-50 hover:shadow-[0px_12px_24px_4px_rgba(255,255,255,0.20)]">
          <div className="block">
            <div className="banner-card relative">
              <img
                src={eventImage}
                alt="image"
                className="lg:h-72 md:h-48 w-full   object-cover object-center max-w-full"
              ></img>
              {liveDisp && (
                <div className="banner-text absolute top-1.5 pl-2 flex flex-row">
                  <svg
                    height="100"
                    width="10"
                    className="blinking pt-2 mr-0.25"
                  >
                    <circle cx="3" cy="3" r="3" fill="red" />
                  </svg>
                  <p>Live</p>
                </div>
              )}
            </div>
            <div className="p-6  hover transition duration-300 ease-in miniitem">
              <h1 className="text-2xl font-semibold mb-3 text-center">
                {eventName}
              </h1>
              <h2 className="text-base  text-indigo-100 mb-1 -mt-2 text-center">
                {eventDate}
              </h2>

              {/* <div className="flex justify-center items-center flex-wrap pt-3">
                <div
                  className="mx-auto  rounded-tr-lg flex justify-start items-center h-8 sm:w-60  hover:opacity-50 cursor-pointer border border-sky-500 xsm:w-full flex justify-center items-center"
                  onClick={handleClick}
                >
                  <a className="text-black-300 inline-flex items-center md:mb-2 lg:mb-0 font-semibold">
                    Read More
                    <svg
                      className="w-4 h-4 ml-2"
                      viewBox="0 0 24 24"
                      stroke="currentColor"
                      stroke-width="2"
                      fill="none"
                      stroke-linecap="round"
                      stroke-linejoin="round"
                    >
                      <path d="M5 12h14"></path>
                      <path d="M12 517 7-7 7"></path>
                    </svg>
                  </a>
                </div>
              </div> */}
            </div>
          </div>
        </div>
      )}
      {isShown && (
        <div className="item h-full border-2 border-gray-200 border-opacity-60 rounded-lg overflow-hidden bg-white  border-none hover:border-stone-50 hover:shadow-[0px_12px_24px_4px_rgba(255,255,255,0.20)] item2 relative">
          <div onClick={handleClick}>
            <div className="flex flex-col justify-between p-6 hover:bg-indigo-700 text-black transition duration-300 ease-i font-mont cursor-pointer font-semibold">
              <h1 className="text-2xl font-semibold mb-3 text-center">
                {eventName}
              </h1>
              <p className="mb-3 text-left">{eventDesc}</p>
              <div className="mt-3 mx-auto  rounded-tr-lg flex justify-start items-center h-8 md:w-60  hover:opacity-50 cursor-pointer border border-sky-500 xsm:w-full flex justify-center items-center ">
                <a
                  href="#"
                  className="text-black-300 inline-flex items-center md:mb-2 lg:mb-0 font-semibold "
                >
                  Register
                  <svg
                    className="w-4 h-4 ml-2"
                    viewBox="0 0 24 24"
                    stroke="currentColor"
                    stroke-width="2"
                    fill="none"
                    stroke-linecap="round"
                    stroke-linejoin="round"
                  >
                    <path d="M5 12h14"></path>
                    <path d="M12 517 7-7 7"></path>
                  </svg>
                </a>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default EventCard;
