import { useEffect, useState } from "react";
import { Disclosure, Menu, Transition } from "@headlessui/react";
import { Bars3Icon, BellIcon, XMarkIcon } from "@heroicons/react/24/outline";

import "../assets/styles/Navbar.css";

function classNames(...classes) {
  return classes.filter(Boolean).join(" ");
}

export default function Navbar() {
  const currentPath = window.location.pathname;

  let str = "";
  const len = currentPath.length;
  let i = 1;
  while (i < len && currentPath[i] != "/") {
    str = str + currentPath[i];
    i++;
  }

  const navigation = [
    { name: "HOMEPAGE", href: "/", current: str == "" },
    { name: "ABOUT", href: "/about", current: str == "about" },
    { name: "SPONSORS", href: "/sponsors", current: str == "sponsors" },
    { name: "EVENTS", href: "/events", current: str == "events" },
    { name: "BLOGS", href: "/blogs", current: str == "blogs" },
    { name: "TEAM", href: "/team", current: str == "team" },
  ];
  // For dynamic navbar background change
  const [scrollPosition, setScrollPosition] = useState(0);
  const [top, setTop] = useState(true);
  const handleScroll = () => {
    const position = window.scrollY;
    setScrollPosition(position);
    if (scrollPosition === 0) {
      setTop(true);
    } else if (scrollPosition > 10) {
      setTop(false);
    } else if (scrollPosition < 10) {
      setTop(true);
    }
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  });

  return (
    <Disclosure
      as="nav"
      // className={`sticky top-0 z-50 ${top ? 'bg-gray-800' : 'bg-white-800'}`}
      className={`sticky top-0 z-50 backdrop-filter ${
        top ? "" : "backdrop-blur-lg"
      } bg-opacity-30`}
      // className={`sticky top-0 z-50 bg-transparent`}
    >
      {({ open }) => (
        <>
          <div className="mx-auto max-w-7xl px-2 sm:px-6 lg:px-8">
            <div className="relative flex h-16 items-center justify-between">
              <div className="absolute inset-y-0 left-0 flex items-center sm:hidden">
                {/* Mobile menu button*/}
                <Disclosure.Button className="inline-flex items-center justify-center rounded-md p-2 text-gray-400 hover:bg-gray-700 hover:text-white focus:outline-none focus:ring-2 focus:ring-inset focus:ring-white">
                  <span className="sr-only">Open main menu</span>
                  {open ? (
                    <XMarkIcon className="block h-6 w-6" aria-hidden="true" />
                  ) : (
                    <Bars3Icon className="block h-6 w-6" aria-hidden="true" />
                  )}
                </Disclosure.Button>
              </div>
              <div className="flex flex-1 items-center justify-center sm:items-stretch sm:justify-start">
                <div className="flex flex-shrink-0 items-center">
                  <img
                    className="block h-10 w-auto lg:hidden"
                    src="https://user-images.githubusercontent.com/77119237/197768334-bafb6521-9502-4fc4-b4b8-37a523e81b4b.png"
                    alt="TZ"
                  />
                  <img
                    className="hidden h-10 w-auto lg:block"
                    src="https://user-images.githubusercontent.com/77119237/197768334-bafb6521-9502-4fc4-b4b8-37a523e81b4b.png"
                    alt="Technozion"
                  />
                </div>
                <div className="hidden sm:ml-6 sm:block">
                  <div className="flex space-x-4">
                    {navigation.map((item) => (
                      <a
                        key={item.name}
                        href={item.href}
                        className={classNames(
                          item.current
                            ? "bg-gray-600 text-white"
                            : "text-white hover:bg-gray-700 hover:text-white",
                          "px-3 py-2 rounded-md text-sm font-medium font-mont"
                        )}
                        aria-current={item.current ? "page" : undefined}
                      >
                        {item.name}
                      </a>
                    ))}
                  </div>
                </div>
              </div>
            </div>
          </div>
          <Disclosure.Panel>
            <div className="space-y-1 px-2 pt-2 pb-3">
              {navigation.map((item) => (
                <Disclosure.Button
                  key={item.name}
                  as="a"
                  href={item.href}
                  className={classNames(
                    item.current
                      ? "bg-gray-900 text-white"
                      : "text-gray-300 hover:bg-gray-700 hover:text-white",
                    "block px-3 py-2 rounded-md text-base font-oswald"
                  )}
                  aria-current={item.current ? "page" : undefined}
                >
                  {item.name}
                </Disclosure.Button>
              ))}
            </div>
          </Disclosure.Panel>
        </>
      )}
    </Disclosure>
  );
}
