import React, { useRef } from "react";
import "../assets/styles/Sponsors.css";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import downarrow from "../assets/images/downarrow.png";
import Anim from "../anim.json";
import Lottie from "lottie-react";

const Sponsors = () => {
  const brands = [
    {
      id: 1,
      name: "",
      image:
        "https://upload.wikimedia.org/wikipedia/sco/thumb/d/d2/Pizza_Hut_logo.svg/2177px-Pizza_Hut_logo.svg.png",
    },
    {
      id: 2,
      name: "",
      image:
        "https://1000logos.net/wp-content/uploads/2020/07/Cheetos-Logo.png",
    },
    {
      id: 3,
      name: "",
      image:
        "https://upload.wikimedia.org/wikipedia/commons/f/f0/Papa_John%27s_Logo_2019.svg",
    },
    {
      id: 4,
      name: "",
      image:
        "https://upload.wikimedia.org/wikipedia/commons/thumb/3/36/McDonald%27s_Golden_Arches.svg/1200px-McDonald%27s_Golden_Arches.svg.png",
    },
    {
      id: 5,
      name: "",
      image:
        "https://upload.wikimedia.org/wikipedia/commons/7/73/Lays_brand_logo.png",
    },
  ];
  var settings1 = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 4,
    slidesToScroll: 1,
    initialSlide: 0,
    autoplay: true,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          infinite: true,
          dots: false,
        },
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
          initialSlide: 2,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };
  var settings2 = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 4,
    slidesToScroll: 2,
    initialSlide: 0,
    autoplay: true,
    rtl: true,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          infinite: true,
          dots: false,
        },
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
          initialSlide: 2,
        },
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };
  const SponsorSection = useRef(null);
  const gotoSponsorSection = () =>
    window.scrollTo({
      top: SponsorSection.current.offsetTop,
      behavior: "smooth",
    });
  return (
    <div className="sponsors-container -mt-16 bg-bgBlue text-white font-oswald">
      <section className="sponsors-hero-container">
        <img
          className="sponsors-header-img"
          src="https://www.advancedsciencenews.com/wp-content/uploads/2022/08/Blue-lights-on-a-black-background.jpg"
        />
        <div className="sponsors-hero-info px-20 py-40">
          <div className="sponsors-hero-title-rect w-12 h-1.5 bg-black mb-2"></div>
          <h1 className="sponsors-hero-title md:text-8xl sm:text-6xl xsm:text-5xl text-bolder font-oswald mx-auto flex justify-start items-center">
            SPONSORS
          </h1>
          <div
            className="pb-2 mx-auto bg-tzpurple rounded-tr-lg h-12 sm:w-60  hover:opacity-50 cursor-pointer mt-48 xsm:w-full flex justify-center items-center scroll-to-main"
            onClick={gotoSponsorSection}
          >
            <Lottie animationData={Anim} />
          </div>
        </div>
      </section>
      <div className="w-8/12 mx-auto" ref={SponsorSection}>
        <Slider {...settings1}>
          {brands.map((item) => {
            return (
              <div className="card h-52 text-white mt-20">
                <div className="card-top overflow-hidden">
                  <img
                    src={item.image}
                    alt={item.title}
                    className="w-36 h-auto mx-auto my-auto"
                  />
                </div>
              </div>
            );
          })}
        </Slider>
      </div>
      <div className="w-8/12 mx-auto mt-12">
        <Slider {...settings2}>
          {brands.map((item) => {
            return (
              <div className="card h-52 text-white">
                <div className="card-top overflow-hidden">
                  <img
                    src={item.image}
                    alt={item.title}
                    className="w-36 h-auto mx-auto my-auto"
                  />
                </div>
              </div>
            );
          })}
        </Slider>
      </div>
      <div className="pb-20">
        <div className="mt-8 text-white text-center text-3xl font-mont">
          Wish to sponsor us?
        </div>
        <div
          className="mx-auto bg-tzpurple rounded-tr-lg flex justify-start items-center h-12 sm:w-60 
        xsm:w-36 hover:opacity-50 cursor-pointer mt-6 flex justify-center items-center text-xl font-semibold font-mont"
        >
          <p>Contact Us</p>
        </div>
      </div>
    </div>
  );
};

export default Sponsors;
