import React from "react";
import "animate.css";
import Sliding from "./Sliding";
import "../assets/styles/Ignosi.css";

function Ignosi() {
  return (
    <div className="bg-bgBlue text-white flex flex-col items-center justify-between space-y-12 md:flex-col md:space-y-0">
      <div className="lines">
        <div className="diamond"></div>
      </div>
      <div className="mx-20 text-center sm:mx-10 xsm:mx-5 xl:px-40">
        <h1 className="text-4xl text-center font-bold	mt-5 font-oswald">
          IGNOSI
        </h1>
        <p className="text-grayishWhite text-lg mt-5 mx-auto text-center font-mont sm:mx-24 xsm:mx-20">
          Lorem Ipsum is simply dummy text of the printing and typesetting
          industry. Lorem Ipsum has been the industry's standard dummy text ever
          since the 1500s
        </p>
      </div>
      <div className="sliding-col container flex flex-col align-center justify-between py-8 mx-auto space-y-4 xl:px-40">
        <Sliding flow1="Left" flow2="Right" disp="text" />
        <Sliding flow1="Right" flow2="Left" disp="image" />
        <div className="pt-12 pb-6">
          <h1 className="text-4xl text-center font-bold font-oswald">
            TECHNOZION
          </h1>
          <p className="text-grayishWhite text-lg mt-5 mx-auto text-center font-mont sm:mx-24 xsm:mx-20">
            Lorem Ipsum is simply dummy text of the printing and typesetting
            industry. Lorem Ipsum has been the industry's standard dummy text
            ever since the 1500s
          </p>
        </div>
        <Sliding flow1="Left" flow2="Right" disp="text" />
      </div>
    </div>
  );
}

export default Ignosi;
