import React from "react";
import { Link } from "react-router-dom";
import '../assets/styles/Blog.css'
import Button from "./UI/Button";

const BlogListItem = props => {
    let redirect = "/blogs/" + props.blog.id;
    return (
        <div className="container">
            <img className="for_image" src={props.blog.imageurl} width={"400px"}/>
            <br />
            <h1 className="for_title font-oswald">{props.blog.title}</h1>
            <h3 className="for_description font-mont">{props.blog.describe}</h3>
            <br />
            {/* <Link to={`/blogs/${props.blog.id}`}> */}
            <a href={redirect}>
                <Button text={"Read More"}/>
            </a>
            {/* </Link> */}
            <hr/>
        </div>
    )
}


export default BlogListItem;